import logo from "./logo.svg";
import "./App.css";
import axios from "axios";
import { useEffect, useRef, useState } from "react";
import emailjs from "emailjs-com";
import ExcelReader from "./ExcelSearch";

export const UsePrevious = (value) => {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  });
  return ref.current;
};

function App() {
  const [Info, setInfo] = useState([]);
  const prevValue = UsePrevious(Info);
  const quantity = [1015, 812, 868, 1046];
  const MINUTE_MS = 300000;
  const Fuel = [
    { searchValue: "А95 - Готівка, банк.картки" },
    { searchValue: "М95 - Готівка, банк.картки" },
    { searchValue: "ДП - Готівка, банк.картки" },
    { searchValue: "МДП+ - Готівка, банк.картки" },
  ];

  useEffect(() => {
    const interval = setInterval(() => {
      FetchWog();
    }, MINUTE_MS);

    return () => clearInterval(interval); // This represents the unmount function, in which you need to clear your interval to prevent memory leaks.
  }, []);

  const sendEmail = (info, searchingValue) => {
    const templateParams = {
      message: `${info.name} WOG Є дизпаливо по картках. ${searchingValue}`,
    };
    emailjs
      .send(
        "service_78bgvog",
        "template_rrocfu4",
        templateParams,
        "CIwkzASVn2LRgUqgZ"
      )
      .then(
        (result) => {},
        (error) => {
          console.log(error.text);
        }
      );
  };

  const wogCheck = (currentInfo, prevInfo, searchingValue) => {
    if (currentInfo.workDescription.includes(searchingValue)) {
      if (prevInfo.length > 0) {
        prevInfo.map((item) => {
          if (item.name === currentInfo.name) {
            if (
              !item.workDescription.includes(searchingValue) &&
              currentInfo.workDescription.includes(searchingValue)
            ) {
               sendEmail(currentInfo, searchingValue)
            }
          }
        });
      } else {
        sendEmail(currentInfo, searchingValue);
      }
    }
  };

  useEffect(() => {
    for (let i = 0; i < quantity.length; i++) {
      if (Info[i]) {
        wogCheck(Info[i], prevValue, Fuel[0].searchValue);
        wogCheck(Info[i], prevValue, Fuel[3].searchValue);
      }
    }
  }, [Info]);

  const FetchWog = () => {
    Promise.all(
      quantity.map((item) => {
        return axios
          .get(`https://api.wog.ua/fuel_stations/${item}`)
          .then((response) => {
            return response.data.data;
          })
          .catch((error) => {
            return error;
          });
      })
    ).then((responses) => {
      setInfo(responses);
    });
  };

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
      <ExcelReader />
    </div>
  );
}

export default App;
