import axios from "axios";
import React, { useState, useEffect } from "react";
import { read, utils } from "xlsx";
import { UsePrevious } from "./App";
import { make_cols } from "./MakeColumns";
import { SheetJSFT } from "./types";
import emailjs from "emailjs-com";

const ExcelReader = () => {
  const [Document, setDocument] = useState();
  const [Data, setData] = useState([]);
  const [Cols, setCols] = useState();
  const prevValue = UsePrevious(Data);
  const MINUTE_MS = 360000;
  const fuelStations = [60112300, 60113200, 60160900, 60113100, 60112100];
  const Fuel = [
    { searchValue: "А-95:" },
    { searchValue: "PULLS 95:" },
    { searchValue: "ДП:"},
    { searchValue: "PULLS Diesel:" },
  ];
  const reader = new FileReader();

  const FetchOkko = async () => {
    await axios
      .get(
        `https://www.okko.ua/api/uk/exportExcel/gas_stations?`,
        { responseType: "blob" },
        {
          headers: {
            "Content-Type":
              "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
          },
        }
      )
      .then((response) => {
        reader.readAsArrayBuffer(response.data);

        reader.onload = (e) => {
          const wb = read(e.target.result, {
            type: "binary",
            bookVBA: true,
          });
          /* Get first worksheet */
          const wsname = wb.SheetNames[0];
          const ws = wb.Sheets[wsname];
          /* Convert array of arrays */
          const data = utils.sheet_to_json(ws);
          /* Update state */
          setData(data);
          setCols(make_cols(ws["!ref"]));
        };
      });
  };

  useEffect(() => {
    const interval = setInterval(() => {
      FetchOkko();
    }, MINUTE_MS);

    return () => clearInterval(interval); // This represents the unmount function, in which you need to clear your interval to prevent memory leaks.
  }, []);

  // const handleChange = (e) => {
  //   const files = e.target.files;
  //   if (files && files[0]) setDocument(files[0]);
  // };

  // const handleFile = () => {
  //   /* Boilerplate to set up FileReader */
  //   const reader = new FileReader();
  //   const rABS = !!reader.readAsBinaryString;

  //   reader.onload = (e) => {
  //     /* Parse data */
  //     const bstr = e.target.result;
  //     const wb = read(bstr, {
  //       type: rABS ? 'binary' : 'array',
  //       bookVBA: true,
  //     });
  //     /* Get first worksheet */
  //     const wsname = wb.SheetNames[0];
  //     const ws = wb.Sheets[wsname];
  //     /* Convert array of arrays */
  //     const data = utils.sheet_to_json(ws);
  //     /* Update state */
  //     setData(data);
  //     setCols(make_cols(ws['!ref']));
  //   };

  //   if (rABS) {
  //     reader.readAsBinaryString(Document);
  //   } else {
  //     reader.readAsArrayBuffer(Document);
  //   }
  // };

  const fuelSearch = (fileStations, searchingStations, searchingFuel, prevValueSearch) => {
    if (
      fileStations.OKKO === ((prevValueSearch)?searchingStations.OKKO:searchingStations)
      ) {  
      if (
        (prevValueSearch)?(!(fileStations.__EMPTY_6.includes("За готівку і банківські картки доступно*:"))):fileStations.__EMPTY_6.includes("За готівку і банківські картки доступно*:")
      ) {
        const dividedString = (prevValueSearch)?searchingStations.__EMPTY_6.split(
          "З паливною карткою і талонами доступно:"
        ):fileStations.__EMPTY_6.split(
          "З паливною карткою і талонами доступно:"
        )
        if (
          dividedString[0].includes(searchingFuel)
        ) {
          return true
        }
      }
    }
  }

  const sendEmail = (info) => {
    const templateParams = {
      message: `${info.__EMPTY_1} Є дизпаливо по картках. OKKO`,
    };
    emailjs
      .send(
        "service_78bgvog",
        "template_rrocfu4",
        templateParams,
        "CIwkzASVn2LRgUqgZ"
      )
      .then(
        (result) => {},
        (error) => {
          console.log(error.text);
        }
      );
  }


  useEffect(() => {
    Data.map((item) => {
      fuelStations.map((station) => {
        if(fuelSearch(item, station, Fuel[3].searchValue, false)){
            if (prevValue.length > 1) {
              prevValue.map((prevItem) => {
                if (fuelSearch(prevItem, item, Fuel[3].searchValue, true)) {
                  sendEmail(item)
                }
              });
            } else {
              sendEmail(item)
            }
          }
    });
  })
  }, [Data]);

  return (
    <div>
{/* //       <label htmlFor="file">Upload an excel to Process Triggers</label>
//       <br />
//       <input
//         type="file"
//         className="form-control"
//         id="file"
//         accept={SheetJSFT}
//         onChange={handleChange}
//       />
//       <br />
//       <input type="submit" value="Process Triggers" onClick={handleFile} /> */}
    </div>
  );
};

export default ExcelReader;
